import React from 'react';
import { Alert } from 'antd';

export default () => (
  <Alert
    className="GameRule"
    message=""
    type="info"
    closeText=""
    description={(
      <div>
        <p>
           keccak256(seed, answer)
           seed answer  0-9 
          
          
          
        </p>
        <ul>
          <li>-1</li>
          <li>0</li>
          <li>1</li>
        </ul>
        <p>
          
           seed  answer 
          
        </p>
      </div>
    )}
  />
);



